import React, {
	Component
} from 'react'
class Img extends Component {
	render() {
		return (
			<figure className="figure">
				<picture>
					<source srcSet={'images/' + this.props.image.fn + '_extra.webp'} media="(min-width: 1200px)" />
					<source srcSet={'images/' + this.props.image.fn + '_large.webp'} media="(min-width: 992px)" />
					<source srcSet={'images/' + this.props.image.fn + '_medium.webp'} media="(min-width: 768px)" />
					<source srcSet={'images/' + this.props.image.fn + '_small.webp'} media="(min-width: 576px)" />
					<img src={'images/' + this.props.image.fn + '_low.webp'} className="figure-img img-fluid rounded" />
				</picture>

				<figcaption className="figure-caption text-right">{this.props.image.fn.toUpperCase().replace(/_/g, ' ')}</figcaption>
			</figure>
		)
	}
}

export default Img


