import React, {
	Component
} from 'react'
import Pic from './Pic.jsx'

class Gallery extends Component {
	constructor(props) {
		super(props)

		this.images = require('./files.json')
		this.sizes = require('./sizes.json')
	}

	chunkify(a, n, balanced) {
		if (n < 2) {
			return [a]
		}

		var len = a.length
		var out = []
		var i = 0
		var size

		if (len % n === 0) {
			size = Math.floor(len / n)
			while (i < len) {
				out.push(a.slice(i, i += size))
			}
		} else if (balanced) {
			while (i < len) {
				size = Math.ceil((len - i) / n--)
				out.push(a.slice(i, i += size))
			}
		} else {
			n--
			size = Math.floor(len / n)
			if (len % size === 0) {
				size--
			}
			while (i < size * n) {
				out.push(a.slice(i, i += size))
			}
			out.push(a.slice(size * n))
		}

		return out
	}

	renderImages(category, chunks, chunkIndex) {
		const result = []
		let images = this.images.filter(function (image) {
			return (image.category || 'unknown') === category
		})
		let imagesForRendering
		if (chunks) {
			const chunkedImages = this.chunkify(images, chunks, true)
			imagesForRendering = chunkedImages[chunkIndex]
		} else {
			imagesForRendering = images
		}

		(imagesForRendering || []).map(function (image, index) {
			result.push(<Pic key={'image-' + index} image={image} />)
		})
		return result
	}

	renderCols() {
		const result = []
		const colSize = this.props.cols || 4
		const bsColSize = 12 / colSize
		for (let i = 0; i < 4; i++) {
			const element = (
				<div className={'col-md-' + bsColSize}>
					{this.renderImages(this.props.name, colSize, i)}
				</div>
			)
			result.push(element)
		}
		return result
	}

	render() {
		return (
			<div className="row">
				{this.renderCols()}
			</div>
		)
	}
}

export default Gallery


